/* Reset default margins/padding */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "articulat-cf", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 12px;
  letter-spacing: 4px;
}

/* The main wrapper to hold everything.
   Leaves 20px gutters on left and right. */
.wrapper-main {
  height: 100vh;
  max-height: 100vh;
  display: grid;
  place-items: center;
  background: #fff; /* Just to visualize background */
}

a {
  text-decoration: none;
  color: black;
}

/* Left-side vertical menu */
.left-text {
  position: fixed;
  bottom: 30px;
  left: 20px;
  display: grid;
  grid-template-columns: repeat(5, auto);
  column-gap: 20px;
  writing-mode: tb-rl;
  transform: rotate(-180deg);
}

/* Add the small spacing (4px) between lines on the left */
.left-text div {
  margin-bottom: 4px;
}

.left-text a {
  display: inline-block; 
  transform-origin: center; 
  transition: transform 0.3s ease;
}

.left-text a:hover {
  transform: scale(1.2);
  color: darkgrey;
}

/* Right-side vertical name */
.right-text {
  position: fixed;
  top: 30px;
  right: 20px;
  letter-spacing: 5px;
  writing-mode: vertical-rl;
  text-orientation: mixed;
}

.name {
  font-family: "articulat-cf", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 14px;
}

/* Middle container for layout */
.middle-container {
  width: calc(100vw - 40px);
  margin: 0 auto;
  display: grid;
  grid-auto-rows: auto;
  gap: 30px; 
}

/* 15 columns grid */
.fifteen-grid {
  display: grid;
  grid-template-columns: repeat(15, 1fr);
}

/* 11 columns grid */
.eleven-grid {
  display: grid;
  grid-template-columns: repeat(11, 1fr);
}

.heading {
  grid-column: 5 / span 2;
  font-family: "articulat-cf", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 14px;
}

.description {
  grid-column: 8 / span 5;
}

.description,
.description li {
  letter-spacing: 2px;
  color: rgb(135, 135, 135);
}

.description p {
  letter-spacing: 2px;
  color: rgb(135, 135, 135);
  margin-bottom: 16px; 
}

ul {
  list-style-type: none;
}

.welcome {
  grid-column: 6 / span 1;
  font-family: "articulat-cf", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 30px;
  text-align: center;
  align-self: center; 
  justify-self: center;
}

.section {
  height: 100vh; /* Full screen height */
  display: flex;
  justify-content: center;
  align-items: center;
}

.email {
  grid-column: 7;
}

.linkedin {
  grid-column: 9;
}

.creative-image {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
}

/* Loader.css */
.loader-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;      /* Full viewport width */
  height: 100vh;     /* Full viewport height */
  background: #000;  /* Black background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;     /* Ensure it overlays all other content */
  opacity: 1;
  transition: opacity 1s ease; /* Fade-out transition */
}

.loader-overlay.fade-out {
  opacity: 0;
}

.loader-video {
  width: 100%;       /* Fill the container horizontally */
  height: 100%;      /* Fill the container vertically */
  object-fit: cover; /* Ensures the video covers the container without leaving black bars */
}


/* ========== MOBILE STYLES ========== */
/* Adjust the max-width as needed for your breakpoints */
@media (max-width: 768px) {
  /* 2.1 Hide the left side menu on mobile */
  .left-text {
    bottom: 30px;
    left: auto;  
    right: 20px;
  }

  /* 2.2 & 2.3: 
     For Introduction and Projects, 
     stack heading and description vertically,
     aligned to the left. 
  */
  .fifteen-grid {
    grid-template-columns: 1fr; /* Single column on mobile */
  }
  .fifteen-grid .heading {
    grid-column: 1 / -1 !important;
    text-align: left;
  }
  .fifteen-grid .description {
    grid-column: 1 / -1 !important;
    text-align: left;
  }

  /* 2.4 For Creative: 
     make it 2 columns with a small gap 
     so it doesn’t overlap right-text
  */
  .eleven-grid {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px; /* small gap */
  }
  .eleven-grid > div {
    /* Override any inline gridColumn */
    grid-column: auto !important;
  }

  /* 2.5: Ensure right-text is not overlapped 
     We can keep position: fixed, just 
     be mindful if you want to reduce font-size or reposition.
  */

  /* 2.6 For Contact: 2 columns on mobile */
  #contact .fifteen-grid {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
  }
  .email,
  .linkedin {
    grid-column: auto !important;
  }

  /* 2.7 Keep .welcome same font size and center screen 
     Override the default grid-column to ensure it’s in the middle
  */
  .welcome {
    grid-column: 1 / -1 !important;
    font-size: 30px; /* same as desktop */
  }

  /* 2.8 Sections still cover full screen height */
  .section {
    height: 100vh;
  }

  /* 2.9 Smaller global font size on mobile (except welcome) */
  body,
  .description,
  .right-text a,
  .name,
  .left-text a,
  .description p,
  .description li {
    font-size: 10px; /* Decrease from 12px */
  }
  .heading{
    font-size: 11px;
  }

  /* (Optional) If you want to reduce letter-spacing on mobile: */
  body,
  .heading,
  .description,
  .right-text a,
  .name {
    letter-spacing: 2px; 
  }

  /* Keep .welcome at 30px */
  .welcome {
    letter-spacing: 4px; /* or same as desktop if you want */
  }

  .middle-container {
    /* Option 1: Use padding on the right */
    padding-right: 30px; /* Adjust this value as needed */
    box-sizing: border-box; 
  }
}
